<template>
  <div class="carType_container">
    <div class="carTypes_wrapper">
      <div class="carTypes_slide" v-for="(item,index) in carTypeList" :key="index">
        <h3 v-text="item.GroupName" @click="clickCarTypeTitle"></h3>
        <div class="carTypes_item" v-for="(item1,index1) in item.GroupList" :key="index1">
          <div class="carType_item" @click="clickCarTypeItem(item1.SerialID)">
            <img :src="item1.Picture"/>
            <div class="carType_info">
              <h4 v-text="item1.AliasName"></h4><span v-text="item1.DealerPrice"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex';
export default {
  props: {
    carTypeList: {
      type: Array,
      default: []
    }
  },
  computed:{
   
  },
  methods:{
    ...mapActions({
      getIsShowCarType: "index/getIsShowCarType"
    }),
    clickCarTypeTitle(){
      this.getIsShowCarType();
    },
    clickCarTypeItem(SerialID){
      this.$router.push({
        path:'/detail',
        query:{
          SerialID: SerialID
        }
      })
    }
  }
};
</script>
<style>
.carType_container {
  width: 75%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
  border-left:solid 1px #ccc;
  transform: translate3d(100%,0,0);
  transition: all 1s;
}
.carType_container.active{
  transform: translate3d(0%,0,0);
}
.carTypes_list,
.carTypes_item {
  width: 100%;
}
.carTypes_list {
  overflow-y: auto;
  overflow-x: hidden;
}
.carTypes_slide h3 {
  background-color: #f3f3f3;
  padding-left: 0.28rem;
  font-size: 0.32rem;
  font-weight: normal;
  color: #999;
}
.carTypes_item,
.carType_item {
  width: 100%;
}
.carType_item {
  display: flex;
  padding: 0.1rem 0.2rem;
  box-sizing: border-box;
  border-bottom: solid 1px #eee;
  position: relative;
}
.carType_item img {
  width: 1.8rem;
  height: 1.2rem;
}
.carType_info {
  flex: 1;
  padding-left: 0.12rem;
}
.carType_info h4 {
  padding-top: 0.08rem;
  font-weight: normal;
  font-size: 0.34rem;
  color: #666;
}
.carType_info span {
  position: absolute;
  padding-top: 0.2rem;
  font-size: 0.28rem;
  color: #f00;
}
</style>



